import { Component, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { Router } from '@angular/router';
import { ActionSheetController } from '@ionic/angular';
import { MenuController } from '@ionic/angular';

@Component({
	selector: 'app-notask',
	templateUrl: './notask.page.html',
	styleUrls: ['./notask.page.scss'],
})
export class NotaskPage implements OnInit {

	constructor(
		public popoverController: PopoverController,
		public actionSheetController: ActionSheetController,
		public router: Router,
		private menu: MenuController
		) { }

	ngOnInit() {
	}

	async presentActionSheet() {
		const actionSheet = await this.actionSheetController.create({
			header: 'Sort Task By',
			buttons: [{
				text: 'All Task',
				// role: 'destructive',
				// icon: 'list-outline',
				handler: () => {
					console.log('All Task clicked');
				}
			}, {
				text: 'Assigned to me',
				// icon: 'person-circle-outline',
				handler: () => {
					console.log('Assigned to me clicked');
				}
			}, {
				text: 'Assigned by me',
				// icon: 'arrow-dropright-circle',
				handler: () => {
					console.log('Assigned by me clicked');
				}
			}, {
				text: 'Cancel',
				// icon: 'close',
				role: 'cancel',
				cssClass:"border-top",
				handler: () => {
					console.log('Cancel clicked');
				}
			}]
		});
		await actionSheet.present();
	}

	async sortActionSheet() {
		const actionSheet = await this.actionSheetController.create({
			header: 'Sort By',
			buttons: [{
				text: 'Due Date',
				// role: 'destructive',
				// icon: 'list-outline',
				handler: () => {
					console.log('Due Date clicked');
				}
			}, {
				text: 'Assignee',
				// icon: 'person-circle-outline',
				handler: () => {
					console.log('Assignee clicked');
				}
			}, {
				text: 'Group',
				// icon: 'arrow-dropright-circle',
				handler: () => {
					console.log('Group');
				}
			}, {
				text: 'Cancel',
				// icon: 'close',
				role: 'cancel',
				cssClass:"border-top",
				handler: () => {
					console.log('Cancel clicked');
				}
			}]
		});
		await actionSheet.present();
	}

	addtask(){
		this.router.navigateByUrl('createtask');
	}


	openFirst() {
		console.log("asdsadsa");
		this.menu.enable(true, 'first');
		this.menu.open('first');
	}
}
